import axios from 'axios';
import {
    createStore
} from 'vuex'
const store = createStore({

    // 데이터를 저장 하는 장소
    // data
    state() {
        return {
            likes: [],
            likesClick: false,
            서버자료: {},
            이름: '홍길동',
            나이: 20
        }
    },

    // 데이터 수정방법을 작성하는 장소
    // methods
    mutations: {
        이름변경(state, payload) {
            state.이름 = payload
        },
        추가(state, payload) {
            state.likes = payload;
        },

        좋아요(state) {
            if (state.likesClick != true) {
                state.likes++;
                state.likesClick = true;
            } else {
                state.likes--;
                state.likesClick = false;
            }
        },
        자료업데이트(state, payload) {
            state.서버자료 = payload;
        }

    },
    // 외부 서버의 데이터를 연동해야 하는 경우
    actions: {
        // context 는 제거될 확률이 높다.
        // context 는 store 자체를 말한다.
        외부자료호출(context) {
            axios.get('https://tarolong.github.io/more0.json').then((res) => {
                // 요청 자료 출력 및 코드 반영
                // console.log(res.data);
                context.commit('자료업데이트', res.data);
            });
        }
    }

})

export default store